<template>
	<div class="todo-list">
		<div v-if="todos && todos.length > 0">
			<TodoItem
				v-for="todo in todos"
				:key="todo._id"
				:_id="todo._id"
				:title="todo.title"
				:description="todo.description"
			/>
		</div>
		<div v-else class="no-todos">No Todos :( Let's Create One!</div>
	</div>
</template>

<script>
import TodoItem from './TodoItem'

export default {
	name: 'TodoList',
	components: {
		TodoItem
	},
	props:{
		todos:{
			required: true,
			type: Array
		}
	}
}
</script>

<style lang="scss" scoped>
.todo-list {
	flex: 1;
}
.no-todos {
	padding: 20px;
	font-size: 25px;
	font-weight: bold;
	text-align: center;
}
</style>
